.gallery {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 6em;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: "Economica",Arial,Helvetica,sans-serif;
}

.gallery > .title {
  font-size: 3em;
  font-family: "Tangerine",Arial, Helvetica, sans-serif;
  margin-bottom: .3em;
  color: #02022b;
  margin-top: .3em;
}

.gallery-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.gallery-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.gallery_pic {
  margin-top: .5em;
  margin-bottom: .5em;
  height: auto;
  max-width: 550px;
  max-height: 275px;
}

