.footer {
  font-family: "Economica",Arial,Helvetica,sans-serif;
  display: -ms-flexbox;
  display: flex;
  color: white;
  background-color: #02022b;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: fixed;
  bottom: 0;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.2em;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
}

@media screen and (max-width: 640px) {
  .address {
    display: none
  }
}

.social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 70px;
}

.fb-icon {
  height: 30px;
  width: 30px;
}

.insta-icon {
  height: 30px;
  width: 30px;
}



