.vouchers {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 6em;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: "Economica",Arial,Helvetica,sans-serif;
}

.vouchers > .title {
  font-family: "Tangerine",Arial, Helvetica, sans-serif;
  font-size: 3em;
  margin-bottom: .3em;
  color: #02022b;
  margin-top: .3em;
}

.vouchers .description {
  font-size: 1.5em;
}

.voucher {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.voucher .title {
  font-family: "Tangerine",Arial, Helvetica, sans-serif;
  font-size: 2.5em;
  margin-bottom: .3em;
  margin-top: .9em;
}

.voucher-element {
  font-size: 1.4em;
}

@media screen and (min-width: 900px) {
  .vouchers .list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
  }
  .vouchers .voucher {
    margin-right: 2em;
    margin-left: 2em;
  }
  .voucher .title {
    margin-top: .7em;
  }
}



